.project-page {
  padding-top: 144.2px;
  padding-bottom: 80px;
  display: flex;
  justify-content: center;
  width: 100%;

  .content-container {
    max-width: 1280px;
    width: 100%;
    padding-left: 0;
    padding-right: 0;
    text-align: left;

    h1 {
      font-size: 32px;
      margin: 0 0 20px;
      color: #333333;
      font-family: 'Amiri', sans-serif;
      font-weight: normal;
      text-align: left;
    }

    .info-section {
      display: flex;
      border-top: 1px solid rgba(34, 34, 34, 0.1);
      padding: 20px 0;

      .info-column,
      .link-column {
        flex: 1; // Give equal space to each column
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        // Add padding to only services and link columns
        &:not(:first-child) {
          padding-left: 40px;
          padding-right: 40px;
        }

        // Only add separators between columns
        &:not(:last-child) {
          border-right: 1px solid rgba(34, 34, 34, 0.1);
        }

        .title {
          font-size: 13px;
          font-family: 'Poppins', sans-serif;
          color: #22222280;
        }

        .detail,
        a {
          font-size: 20px;
          font-family: 'Amiri', sans-serif;
          color: #333333;
          margin-top: 5px;
          text-decoration: none; // Remove underline from link
        }
      }
    }

    .dashboard-image {
      width: calc(100% - 80px); // Subtract the left and right padding from the width
      max-width: 1280px; // Limit the size of the image to match the container
      height: auto; // Maintain the aspect ratio
      margin-top: 100px; // Add some space above the image
      margin-bottom: 20px; // Add some space below the image
      display: block; // Ensures the image is treated as a block-level element
      margin-left: auto; // These two margins center the image
      margin-right: auto;
      padding: 0 40px; // Add padding on the left and right
    }

    // Merged styles for the project summary text
    .project-summary-text {
      font-size: 32px;
      font-family: 'Amiri', sans-serif;
      color: #333333;
      margin: 100px auto 40px; // Top, right/left (auto for centering), bottom
      padding: 0 40px; // Left and right padding
      max-width: 770px; // Maximum width of the text block
      display: block; // Ensure that this is a block element
      text-align: center; // Center the text
      margin-bottom: 40px;
    }
    figure {

      text-align: center; // Center the figure content

      // Styles for the new project image under the project summary
      .project-detail-image {
        display: block; // Ensure the image is treated as a block-level element
        max-width: 100%; // Limit the size of the image to the width of its container
        height: auto; // Maintain the aspect ratio of the image
        margin-top: 20px; // Space above the image
        // margin-bottom: 40px; // Space below the image
        margin-left: auto; // Center the image
        margin-right: auto;
        border: 1px solid black; // Thin black border around the image
      }
      figcaption {
        margin-top: 5px;
        font-size: 16px;
        font-style: italic;
        font-family: 'Amiri', sans-serif; // Amiri font for the caption
        text-align: center;
      }
    }
  }
}

// Responsive styles
@media (max-width: 768px) {
  .project-page .content-container h1 {
    font-size: 24px;
  }

  .project-page .content-container .info-section {
    flex-direction: column;
    border-top: none;

    .info-column,
    .link-column {
      padding-left: 0; // Remove padding for mobile view
      padding-right: 0; // Remove padding for mobile view
      border-right: none;
      padding-bottom: 1rem;

      &:not(:last-child) {
        border-bottom: 1px solid rgba(34, 34, 34, 0.1);
      }
    }
  }

  .project-page .content-container .project-summary-text {
    padding: 80px 20px; // Top/bottom, left/right padding for smaller screens
    text-align: left; // Align text to the left on smaller screens if preferred
  }
}

@media (max-width: 480px) {
  .project-page .content-container h1 {
    font-size: 20px;
  }

  .project-page .content-container .project-summary-text {
    padding: 40px 20px; // Further adjust padding for very small screens
    text-align: left; // Align text to the left on very small screens if preferred
  }
}
